import React from "react";
import { useTranslation } from "react-i18next";

const ReviewsSection = () => {
  const { t } = useTranslation();

  // تحميل آراء العملاء من الترجمة
  const reviews = t("reviews.reviewsList", { returnObjects: true });

  // التحقق إذا كانت الآراء غير موجودة أو ليست مصفوفة
  if (!reviews || !Array.isArray(reviews)) {
    return (
      <section className="reviews-section py-2">
        <div className="container">
          <h2 className="text-center mb-4">{t("reviews.heading")}</h2>
          <p className="text-center">{t("reviews.noReviews")}</p>
        </div>
      </section>
    );
  }

  return (
    <section className="reviews-section py-2" id="customer-reviews">
      <div className="container">
        {/* تحسين SEO */}
        <h2 className="text-center mb-4">
          {t("reviews.heading")} - G. GROUP S.R.L.
        </h2>
        <p className="text-center">
          {t("reviews.subHeading", { company: "G. GROUP S.R.L." })}
        </p>
        <div className="row">
          {reviews.map((review, index) => (
            <article className="col-md-4 mb-4" key={index}>
              <div className="card shadow-sm">
                <div className="card-body">
                  <h5 className="card-title">{review.name}</h5>
                  <p className="card-text">"{review.feedback}"</p>
                  <div className="rating">
                    {Array.from({ length: review.rating }, (_, i) => (
                      <span key={i} className="text-warning">
                        &#9733;
                      </span>
                    ))}
                    {Array.from({ length: 5 - review.rating }, (_, i) => (
                      <span key={i} className="text-muted">
                        &#9733;
                      </span>
                    ))}
                  </div>
                </div>
              </div>
            </article>
          ))}
        </div>
        {/* زر لعرض جميع الآراء */}
        <div className="text-center">
          <a href="/testimonials" className="btn btn-primary">
            {t("reviews.more")}
          </a>
        </div>
      </div>
    </section>
  );
};

export default ReviewsSection;
